import * as React from 'react';
import Box from '@mui/material/Box';

export const FadeIn = ({
  children,
  sx,
  disableInteraction = true,
  threshold = 0
}: {
  children: React.ReactNode;
  sx?: any;
  disableInteraction?: boolean;
  threshold?: number;
}): React.JSX.Element => {
  const ref = React.useRef(null);
  const [inView, setInView] = React.useState(false);

  React.useEffect(() => {
    if (!ref.current) return;

    const io = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          console.log('inview');
          setInView(true);
          io.disconnect(); // 1回だけ
        }
      },
      { threshold }
    );

    io.observe(ref.current);

    return () => io.disconnect();
  }, []);

  return (
    <Box
      position='relative'
      width='100%'
      sx={{
        userSelect: 'none',
        ...sx
      }}
      onContextMenu={(e: any) => {
        e.preventDefault();
      }}
    >
      <Box
        sx={{
          display: disableInteraction ? 'block' : 'none',
          position: 'absolute',
          width: '100%',
          height: '100%',
          zIndex: 2
        }}
      ></Box>
      <Box
        sx={{
          backgroundColor: '#ddd'
        }}
      >
        <Box
          ref={ref}
          sx={{
            transition: ['opacity 500ms ease-out 1000ms'].join(','),
            opacity: inView ? 1 : 0
          }}
        >
          {children}
        </Box>
      </Box>
    </Box>
  );
};
