import * as React from 'react';

export const Video = ({
  src,
  muted = true,
  controls = true,
  loop = false,
  threshold = 0.5,
  style
}: {
  src: string;
  muted?: boolean;
  controls?: boolean;
  loop?: boolean;
  threshold?: number;
  style?: React.CSSProperties;
}): React.JSX.Element => {
  const videoRef = React.useRef<HTMLVideoElement>(null);

  React.useEffect(() => {
    const video = videoRef.current;
    if (!video) return;

    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          video.play().catch(() => {
            // iOSなどで失敗する場合があるため握りつぶす
          });
        } else {
          video.pause();
        }
      },
      {
        threshold
      }
    );

    observer.observe(video);

    return () => {
      observer.unobserve(video);
      observer.disconnect();
    };
  }, []);

  return (
    <video
      // @note iPhone Safari で動画再生前に何も表示されず真っ白になる問題の対応のため #t= を追加
      // src={`https://cdn.yuheiono.com/videos/${image.name}#t=0.001`}
      src={src}
      ref={videoRef}
      // width={floatWidth}
      // height={floatHeight}
      muted={muted}
      controls={controls}
      loop={loop}
      playsInline={true}
      disablePictureInPicture={true}
      autoPlay={false}
      // preload='auto'
      preload='metadata'
      controlsList={['nodownload', 'nofullscreen', 'noplaybackrate'].join(' ')}
      onContextMenu={e => {
        e.preventDefault();
      }}
      style={{
        display: 'block',
        maxWidth: '100%',
        backgroundColor: '#ddd',
        ...style
      }}
    />
  );
};
